<template>
    <section>
    </section>
</template>
<script>
    import wx from 'weixin-js-sdk'
    import * as config from '../../../unit/config'
    import {mapGetters} from 'vuex'

    export default {
        name: "share",
        async mounted() {
            let that = this;
            /**
             *  
             * */
            await that.$unit.makeONlinLInk(config.CONST_REQUEST_URI, config.CONST_SHARE_URI, config.CONST_APP_TOKEN, {}).then(res => {
                let shareInfo = {
                    title: '',
                    desc: '',
                    link: res,
                    img: config.CONST_RESPEC_URI + '/h/' + config.CONST_APP_IC + '/dist/share.jpeg'
                };
                console.log(shareInfo);
                /**
                 * 
                 * */
                if (that.$unit.isWeiXin()) {
                    that.weiXinShare(shareInfo);
                } else {
                    console.log('');
                }
            });
        },
        methods: {
            async weiXinShare(info) {
                let that = this;
                let _url = window.location.href;

                /**
                 * wxConfig
                 * */
                await that.$store.dispatch('_getWxConfig', {
                    im: config.requestInterface.getJsconf,
                    fps: {
                        'url': encodeURIComponent(_url)
                    },
                    url: config.CONST_FETCH_REQUEST_URI
                });

                wx.config({
                    debug: false,
                    appId: that.getwxconfigInfo.appId, // 
                    timestamp: that.getwxconfigInfo.timestamp, // 
                    nonceStr: that.getwxconfigInfo.nonceStr, // 
                    signature: that.getwxconfigInfo.signature, // 1
                    jsApiList: JSON.parse(that.getwxconfigInfo.jsApiList_json), //  JS JS2
                });

                wx.ready(function () {
                    /**
                     * 
                     * */
                    wx.onMenuShareAppMessage({
                        title: info.title, // 
                        desc: info.desc, // 
                        link: info.link, // 
                        imgUrl: info.img, // 
                        type: '', // ,musicvideolinklink
                        dataUrl: '', // typemusicvideo
                        success: function () {
                            console.log('')
                        },
                        cancel: function () {
                            alert('')
                            window.location.reload();
                        }
                    });
                    /**
                     * 
                     * */
                    wx.onMenuShareTimeline({
                        title: info.title, // 
                        desc: info.desc, // 
                        link: info.link, // 
                        imgUrl: info.img, // 
                        success: function () {
                            console.log('')
                        },
                        cancel: function () {
                            alert('')
                            window.location.reload();
                        }
                    });
                });
            },
            /**
             * 
             * */
            getAddCard(params) {
                return new Promise(resolve => {
                    wx.addCard({
                        cardList: [
                            {
                                cardId: params.cardId,
                                cardExt:
                                    '{"openid":"' +
                                    params.openid +
                                    '",' +
                                    '"nonce_str":"' +
                                    params.nonce_str +
                                    '",' +
                                    '"timestamp":"' +
                                    params.timestamp +
                                    '",' +
                                    '"signature":"' +
                                    params.signature +
                                    '"}'
                            }
                        ],
                        success() {
                            resolve(true);
                        }
                    });
                });
            }
        },
        computed: {
            ...mapGetters([
                'getOpenidInfo',
                'getwxconfigInfo'
            ])
        }
    }
</script>